  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.color-nav
{
    background-color: #060b26;
    /*background-color: darkkhaki;*/
    /* position: relative; */
    position: fixed !important;
    z-index: 1000 !important;
    width: 100% !important;
    
}

.color-nav a{
    color: #f5f5f5 !important;
}

.name
{
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
}
.right-menu
{
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 100%;
    -webkit-justify-content: right;
            justify-content: right;
}

.sidebardata
{
  background-color:#060b26;
  /*background-color: darkkhaki;*/
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-align-items: center;
          align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;  
}

.menu-bars
{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu
{
    background-color: #060b26;
    /*background-color: darkkhaki;*/
    width: 250px;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active
{
    left: 0 !important;
    transition: 350ms;
    z-index: 99 !important;
}

.nav-text
{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a
{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;

}

.nav-menu.items
{
    width: 100%;
}

.navbar-toggle
{
    background-color: #060b26;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    height: 80px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.nav-text a:hover
{
    background: color #1a83ff;
}

span
{
    margin-left: 16px;
}

.custom-img {
    height: 90vh;
    object-fit: cover;
  }

